* {
    box-sizing: border-box;
}
.container {
    max-width: 1350px;
    margin: 0 auto;
}
body {
    font-family: "Nunito Sans", sans-serif;
}
.section-header {}
.section-title {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
    position: relative;
    padding-bottom: 20px;
    margin-top: 13px;
}
.section-title:after {
    content: "";
    width: 130px;
    height: 2px;
    position: absolute;
    background-color: $colorPrimary;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}
.section-title-text {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    display: block;
    margin-top: 30px;
}

.btn {
    display: inline-block;
    padding: 20px 50px;
    text-transform: uppercase;
    color: #fff;
    border: 0;
    background: $colorSecondary;
    border-radius: 40px;
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    font-weight: 700;
    transition: 0.4s all;
    cursor: pointer;
}

.btn:hover {
    background: $colorPrimary;
}

.loading {
    position: relative;
}

.loading:before {
    content: "";
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    border: 2px solid rgba(#fff, 0.2);
    border-right-color: #fff;
    border-radius: 50%;
    animation: aniLoading 0.5s 0s infinite;
}
@keyframes aniLoading {
    0% {transform: translateY(-50%), rotate(0deg);}
    100% {transform: translateY(-50%), rotate(360deg);}
}
